@import 'color';
@import 'chart';
@import 'fontello';
@import 'panel';
@import 'blockly-toolbox';
@import 'toolbox';
@import 'reality-check';

html, body {
  height: 100%;
  margin: 0em;
  max-width: 100%;
  max-height: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
}

#blocklyDiv {
  position: absolute;
  height: 100%;
  width: 100%;
}

#center {
  position: fixed;
  width: 1em;
  height: 1em;
  top: 50%;
  left: 50%;
}

.barspinner {
  position: fixed;
  left: 50%;
  top: 50%;
  margin-left: -5rem;
  z-index: 9999;
}

.icon-remove:before {
  content: 'X';
}

#header select, #topbar select {
  color: #000;
  font-size: 1em;
}

#header {
  height: 81px;
  width: 100%;
  background-color: $brand-blue;
  border-top: 2px solid $brand-dark-blue;
  border-bottom: 4px solid $brand-orange;
  font-size: 18px;
  padding: 0 20px;
}

#topbar {
  background-color: $brand-primary-dark;
  width: 100%;
  color: $white;
  line-height: 1;
  font-size: 70%;
  text-align: right;
  padding: 3px 20px;
  display: table;
}

.left-header {
  float: left;
}

.right-header {
  float: right;
}

#client-logged-in {
  display: none;
}

.topMenuBalance {
  visibility: hidden;
}

#login {
  display: none;
  margin: 0.6em 0;
}

.account {
  font-size: 16px;
}

.btn-primary {
  color: $white !important;
  text-decoration: none !important;
  background: $brand-btn !important;
  border-color: $brand-btn !important;
}

.btn-primary:active, .btn-primary:hover {
  color: $white !important;
  text-decoration: none !important;
  background-color: $brand-btn-active !important;
  border-color: $brand-btn-active !important;
}

.blocker {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 1000;
  display: none;
  top: 0em;
  opacity: 0.1;
  left: 0em;
  background: black;
}

#stopButton {
  display: none;
}

select.tour-highlight {
  background: white;
}

.actions_menu {
  display: none;
}

.languages {
  z-index: 999;
}

.tours {
  z-index: 999;
  position: relative;
  margin-right: 240px;
  font-size: 13px;
}
#display-tour, #select-tour {
  position: absolute;
  width: 116px;
  padding: 0;
  margin: 0;
  text-align: center;
}
#select-tour {
  background-color: #F2F2F2;
  border: 1px solid #DEDEDE;
  color: black;
  display: none;
}
#select_language, #select-tour {
  top: -3px;
}
#select-tour li {
  cursor: pointer;
  padding: 3px;
  margin: 0;
}
#display-tour li, #display_language li {
  cursor: pointer;
  line-height: 0;
}
#container {
  position: fixed;
  height: calc(100% - 96px);
  width: 100%;
  left: 0px;
  transition: 0.5s;
}
